#questions {
  max-width:80em;
  margin:auto;
}
#questions li {
  cursor: pointer;
}
.accordion {
  border-bottom: 2px solid;
  height: 0;
  margin-bottom: 1em;
  overflow: hidden;
  padding: 0.5em 0 0;
}
.accordion p {
  max-width:35em;
  margin-left:1.5em;
}
.expand .accordion {
  height: 8em;
}
.open:before {
  content: "+";
  display: inline-block;
  width: 1em;
}
.expand span:before {
  content: "-";
}
